<template>
    <div>
        <h2>{{ title }}</h2>
<!--        <b-img :src='imageUrl'></b-img>-->
        <b-img :src="'http://localhost:9000/display?imageName=' + imageUrl"></b-img>
        <viewer v-if="content != null" :initialValue="content"/>
<!--        <ToastViewer :toast-view-data='content'/>-->
    </div>
</template>

<script>
    // import ToastViewer from "../../components/ToastViewer";
    import { getDetail } from "../../api";

    export default {
        name: "Detail",
        components: {
            // ToastViewer
        },
        data() {
            return {
                bid: this.$route.params.bid,
                title: '',
                price: 0,
                content: null,
                writer: '',
                imageUrl: '',
                imageData: ''
            }
        },
        created() {
            getDetail('/goods/detail', this.bid)
                .then(({data}) => {
                    this.content = data.content
                    this.title = data.title
                    this.imageUrl = data.image.path + '/' + data.image.uuid + '_' + data.image.fileName
                    console.log(this.imageUrl)
                    // displayGoodsImage(this.imageUrl)
                    //     .then((response) => {
                    //         console.log(response.data)
                    //         this.imageData = 'data:image/jpeg;base64,' + response.data
                    //     })
                })

        },
        mounted() {

        }
    }
</script>

