<template>
    <div>
        <h6>params 카테고리 넘버: {{ $route.params.categoryNum }}</h6>
        <h6>props 카테고리 넘버: {{ categoryNum }}</h6>
        <b-table
                small
                hover
                :fields="fields"
                :items="goods"
                responsive="sm"
                @row-clicked="clickRow"
        >
            <template v-slot:cell(title)="data">
                <b>{{ data.value }}</b>
            </template>
        </b-table>

        <router-link to="/goods/write">
            <b-button v-if="checkAdmin" squared variant="outline-secondary">글쓰기</b-button>
        </router-link>
    </div>
</template>

<script>
    export default {
        name: "Goods",
        computed: {
          checkAdmin() {
              if (!this.$store.getters.getAuth.includes('ROLE_ADMIN'))
                  return false
              return true
          }
        },
        props: {
            categoryNum: {
                type: String,
                default: ''
            }
        },
        data() {
            return {
                fields: [
                    'gid',
                    { key: 'title', label: '제목'}
                ],
                goods: null,
            }
        },
        created() {
            this.$store.dispatch("getGoods", this.$route.params.categoryNum)
                .then(() => {
                    this.goods = this.$store.state.goods.goods
                })
        },
        methods: {
            clickRow(record) {
                this.$router.push(`/goods/${this.categoryNum}/${record.gid}`)
            }
        }
    }
</script>

